"use client";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { useProjectStore } from "@/store/useProjectStore";
import type { ChatMessage as ChatMessageType } from "@/types";
import { format } from "date-fns";
import { Bot, Check, Copy, Trash2, User } from "lucide-react";
import { useState } from "react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { vscDarkPlus } from "react-syntax-highlighter/dist/cjs/styles/prism";

interface ChatMessageProps {
  message: ChatMessageType;
  onDelete?: (messageId: string) => void;
}

// Parse code blocks from the message
function parseMessage(content: string) {
  const regex = /```(\w+)?\n([\s\S]*?)```/g;
  const parts: { type: "code" | "text"; content: string; language?: string }[] =
    [];

  let lastIndex = 0;
  let match: RegExpExecArray | null;

  match = regex.exec(content);
  while (match !== null) {
    if (match.index > lastIndex) {
      parts.push({
        type: "text",
        content: content.slice(lastIndex, match.index),
      });
    }
    parts.push({
      type: "code",
      content: match[0],
      language: match[1] || "",
    });
    lastIndex = regex.lastIndex;
    match = regex.exec(content);
  }

  if (lastIndex < content.length) {
    parts.push({
      type: "text",
      content: content.slice(lastIndex),
    });
  }

  return parts;
}

export function ChatMessage({ message, onDelete }: ChatMessageProps) {
  const [copied, setCopied] = useState(false);
  const store = useProjectStore();

  const isUser = message.sender === "user";
  const parsedParts = parseMessage(message.content);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(message.content);
      setCopied(true);
      setTimeout(() => setCopied(false), 1500);
    } catch {
      setCopied(false);
    }
  };

  const handleDelete = () => {
    if (onDelete) onDelete(message.id);
  };

  return (
    <div className={`flex gap-3 ${isUser ? "flex-row-reverse" : ""}`}>
      {/* Avatar */}
      <div
        className={`flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center ${
          isUser
            ? "bg-blue-500 text-white"
            : "bg-gradient-to-br from-purple-500 to-blue-500 text-white"
        }`}
      >
        {isUser ? <User className="w-4 h-4" /> : <Bot className="w-4 h-4" />}
      </div>

      {/* Content */}
      <div
        className={`flex-1 max-w-[80%] flex flex-col ${
          isUser ? "items-end" : "items-start"
        }`}
      >
        {/* Agent badge + timestamp as headline */}
        {!isUser && (
          <div className="flex items-center gap-2 mb-1">
            {message.agentName && (
              <span
                key={message.agentName}
                className="font-bold text-lg text-primary"
              >
                {message.agentName}
              </span>
            )}
            <span className="text-xs text-muted-foreground">
              {format(new Date(message.timestamp), "HH:mm")}
            </span>
          </div>
        )}
        {/* For user, show 'You' as headline */}
        {isUser && (
          <div className="flex items-center gap-2 mb-1 justify-end">
            <span className="font-bold text-lg text-blue-600">You</span>
            <span className="text-xs text-muted-foreground">
              {format(new Date(message.timestamp), "HH:mm")}
            </span>
          </div>
        )}

        {/* Message bubble */}
        <div
          className={`group relative rounded-lg px-4 py-3 ${
            isUser
              ? "bg-blue-500 text-white ml-8"
              : "bg-muted text-foreground mr-8"
          }`}
        >
          <div className="space-y-3 text-sm leading-relaxed">
            {parsedParts.map((part) =>
              part.type === "code" ? (
                <div key={`${part.type}-${part.content}`} className="relative">
                  <SyntaxHighlighter
                    language={part.language}
                    style={vscDarkPlus}
                    customStyle={{
                      borderRadius: "0.5rem",
                      padding: "0.75rem",
                      backgroundColor: isUser ? "#2563eb" : "#1e1e1e",
                      color: "#d4d4d4",
                      fontSize: "0.85rem",
                    }}
                  >
                    {part.content.trim()}
                  </SyntaxHighlighter>
                  {/* Insert to Editor button for agent code blocks */}
                  {!isUser && (
                    <Button
                      variant="outline"
                      size="sm"
                      className="absolute top-2 right-2 z-10"
                      onClick={() => {
                        // Generate a filename if not present
                        const filename = `AgentCode_${Date.now()}.${
                          part.language || "txt"
                        }`;
                        store.openFile({
                          id: Math.random().toString(36).substring(7),
                          name: filename,
                          path: filename,
                          content: part.content,
                          language: part.language,
                          type: "file",
                        });
                      }}
                    >
                      Insert to Editor
                    </Button>
                  )}
                </div>
              ) : (
                <p
                  key={`${part.type}-${part.content}`}
                  className="whitespace-pre-wrap"
                >
                  {part.content}
                </p>
              )
            )}
          </div>

          {/* Buttons */}
          <div
            className={`absolute ${
              isUser ? "top-2 left-2" : "top-2 right-2"
            } opacity-0 group-hover:opacity-100 transition-opacity flex gap-1`}
          >
            <Button
              variant="ghost"
              size="sm"
              className="h-6 w-6 p-0"
              onClick={handleCopy}
            >
              {copied ? (
                <Check className="w-3 h-3" />
              ) : (
                <Copy className="w-3 h-3" />
              )}
            </Button>

            {onDelete && (
              <Button
                variant="ghost"
                size="sm"
                className="h-6 w-6 p-0 text-red-500 hover:text-red-700"
                onClick={handleDelete}
              >
                <Trash2 className="w-3 h-3" />
              </Button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
