"use client";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Spinner } from "@/components/ui/spinner";
import { Textarea } from "@/components/ui/textarea";
import { ChatService } from "@/services/chatService";
import { useProjectStore } from "@/store/useProjectStore";
import {
  Bot,
  FolderOpen,
  MoreVertical,
  Plus,
  Send,
  Trash2,
  User,
} from "lucide-react";
import { useState, useEffect } from "react";
import { toast } from "sonner";
import { ChatMessage } from "./ChatMessage";
import { TypingIndicator } from "./TypingIndicator";

// Add at the top of the file
declare global {
  interface Window {
    injectTerminalCommand?: (command: string) => void;
  }
}

export function ChatInterface() {
  const [inputValue, setInputValue] = useState("");
  const {
    chatSessions,
    activeChatId,
    isTyping,
    addMessageToActiveChat,
    setIsTyping,
    activeAgents,
    availableAgents,
    toggleAgent,
    clearMessages,
    createNewChatSession,
    deleteMessageFromActiveChat,
    toggleChatHistory,
    isChatHistoryOpen,
    project,
    openTabs,
    uploadFile,
    openFile,
    loadFiles,
    setActiveArea,
  } = useProjectStore();

  const [streaming, setStreaming] = useState(false);
  const [streamedText, setStreamedText] = useState("");
  const [isStreaming, setIsStreaming] = useState(false);

  // Ensure 'Coder' agent is default and active on mount or when availableAgents changes
  useEffect(() => {
    if (Array.isArray(availableAgents) && availableAgents.length > 0) {
      const coderAgent = availableAgents.find(
        (a) => a.name?.toLowerCase() === "coder"
      );
      if (coderAgent && !coderAgent.isActive) {
        toggleAgent(String(coderAgent.id));
      } else if (!coderAgent) {
        // If no 'Coder', activate the first agent if none are active
        const anyActive = availableAgents.some((a) => a.isActive);
        if (!anyActive) {
          toggleAgent(String(availableAgents[0].id));
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [availableAgents]);

  // Get the active chat session and its messages
  const activeChat = chatSessions.find((chat) => chat.id === activeChatId);
  const messages = activeChat?.messages || [];

  const handleSendMessage = async () => {
    if (!inputValue.trim()) return;
    addMessageToActiveChat({
      id: Math.random().toString(36).substring(7),
      content: inputValue,
      sender: "user",
      timestamp: new Date(),
      type: "text",
    });
    const messageToSend = inputValue;
    setInputValue("");
    setIsTyping(true);
    try {
      const activeAgentIds = (Array.isArray(activeAgents) ? activeAgents : [])
        .filter((a) => a.isActive)
        .map((a) => a.id);
      const data = await ChatService.sendMessage({
        chatId: activeChatId,
        projectId: project?.id || "default-project",
        message: messageToSend,
        activeAgents: activeAgentIds,
      });
      // Add AI responses
      for (const msg of data.messages) {
        // Type guard for choices
        let content = "";
        if (
          msg &&
          "choices" in msg &&
          Array.isArray(msg.choices) &&
          msg.choices.length > 0 &&
          msg.choices[0].message &&
          typeof msg.choices[0].message.content === "string"
        ) {
          content = msg.choices[0].message.content ?? "";
        } else if (typeof msg.content === "string") {
          content = msg.content;
        }
        addMessageToActiveChat({
          id: Math.random().toString(36).substring(7),
          content: content ?? "",
          sender: "ai",
          agentName: String(
            msg && "agentName" in msg && typeof msg.agentName === "string"
              ? msg.agentName
              : "AI"
          ),
          timestamp: new Date(),
          type: "text",
        });
      }
      // If files are returned, add them to the file tree and open in editor
      if (data.files && Array.isArray(data.files)) {
        for (const file of data.files) {
          await uploadFile({ filePath: file.filePath, content: file.content });
          openFile({
            id: Math.random().toString(36).substring(7),
            name: file.filePath.split("/").pop() || file.filePath,
            path: file.filePath,
            content: file.content,
            language: file.filePath.split(".").pop() || "text",
            type: "file",
          });
        }
        // Refresh file tree after all files are handled
        if (project?.id) await loadFiles(project.id);
      }
      toast.success("Message sent to agent(s)");
    } catch (error) {
      console.error("Chat API error:", error);
      addMessageToActiveChat({
        id: Math.random().toString(36).substring(7),
        content: "Failed to get AI response. Please try again.",
        sender: "ai",
        agentName: "System",
        timestamp: new Date(),
        type: "text",
      });
      toast.error("Failed to send message to agent(s)");
    } finally {
      setIsTyping(false);
    }
  };

  const handleNewChat = () => {
    createNewChatSession("New Chat");
  };

  const handleClearChat = () => {
    clearMessages();
  };

  const handleSendMessageStream = async () => {
    if (!inputValue.trim()) return;
    setStreamedText("");
    setIsStreaming(true);
    const messages = [
      { role: "system", content: "You are a helpful AI developer assistant." },
      { role: "user", content: inputValue },
    ];
    try {
      const stream = await ChatService.sendMessageStream({ messages });
      const reader = stream.getReader();
      const decoder = new TextDecoder("utf-8");
      let done = false;
      let buffer = "";
      while (!done) {
        const { value, done: doneReading } = await reader.read();
        done = doneReading;
        if (value) {
          buffer += decoder.decode(value, { stream: true });
          // Parse SSE: split on double newlines
          const parts = buffer.split("\n\n");
          buffer = parts.pop() || "";
          for (const part of parts) {
            if (part.startsWith("data: ")) {
              const chunk = part.replace("data: ", "");
              if (chunk === "[DONE]") {
                setIsStreaming(false);
                return;
              }
              setStreamedText((prev) => prev + chunk);
            }
          }
        }
      }
    } catch (err) {
      setIsStreaming(false);
      toast.error("Streaming error: " + (err as Error).message);
    }
    setIsStreaming(false);
  };

  const handleWorkflow = async () => {
    if (!inputValue.trim()) return;
    addMessageToActiveChat({
      id: Math.random().toString(36).substring(7),
      content: inputValue,
      sender: "user",
      timestamp: new Date(),
      type: "text",
    });
    const messageToSend = inputValue;
    setInputValue("");
    setIsTyping(true);
    try {
      // Gather open file contents as context
      const context = openTabs.map((tab) => ({
        path: tab.path,
        content: tab.content,
        language: tab.language,
      }));
      const data = await ChatService.sendWorkflowMessage({
        chatId: activeChatId || "default-chat",
        message: messageToSend,
        projectId: project?.id || "default-project",
        context,
      });
      // Add each agent's response as a chat message
      for (const resp of data.responses as {
        agentName: string;
        content: string;
      }[]) {
        addMessageToActiveChat({
          id: Math.random().toString(36).substring(7),
          content: resp.content,
          sender: "ai",
          agentName: resp.agentName,
          timestamp: new Date(),
          type: "text",
        });
        // Detect and inject terminal commands
        const terminalCommand = extractTerminalCommand(resp.content);
        if (terminalCommand) {
          setActiveArea("terminal");
          if (typeof window !== "undefined" && window.injectTerminalCommand) {
            window.injectTerminalCommand(terminalCommand);
          }
        }
        // Optionally, set active area for editor, files, or preview based on agent
        if (resp.agentName?.toLowerCase().includes("coder"))
          setActiveArea("editor");
        if (resp.agentName?.toLowerCase().includes("file"))
          setActiveArea("files");
        if (resp.agentName?.toLowerCase().includes("preview"))
          setActiveArea("preview");
      }
      // If files are returned, add them to the file tree and open in editor
      if (data.files && Array.isArray(data.files)) {
        for (const file of data.files) {
          await uploadFile({ filePath: file.filePath, content: file.content });
          openFile({
            id: Math.random().toString(36).substring(7),
            name: file.filePath.split("/").pop() || file.filePath,
            path: file.filePath,
            content: file.content,
            language: file.filePath.split(".").pop() || "text",
            type: "file",
          });
        }
        // Refresh file tree after all files are handled
        if (project?.id) await loadFiles(project.id);
      }
      toast.success("Workflow sent to agent(s)");
    } catch (error) {
      console.error("Workflow error:", error);
      addMessageToActiveChat({
        id: Math.random().toString(36).substring(7),
        content: "Workflow failed. Please try again.",
        sender: "ai",
        agentName: "System",
        timestamp: new Date(),
        type: "text",
      });
      toast.error("Failed to send workflow to agent(s)");
    } finally {
      setIsTyping(false);
    }
  };

  return (
    <div className="flex flex-col h-full">
      {/* Agent Selection UI */}
      <div className="p-4 border-b bg-card flex items-center gap-3">
        <span className="text-xs text-muted-foreground mr-2 font-medium">
          Select Agent:
        </span>
        {Array.isArray(availableAgents) && availableAgents.length > 0 ? (
          (() => {
            // Find the active agent, fallback to 'Coder', then first agent
            const activeAgent =
              availableAgents.find((a) => a.isActive) ||
              availableAgents.find((a) => a.name?.toLowerCase() === "coder") ||
              availableAgents[0];
            const agentLabel =
              activeAgent && activeAgent.name
                ? activeAgent.name
                : "Coder Agent";
            return (
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button
                    variant="outline"
                    className="flex items-center gap-2 rounded-full px-5 py-2 border-2 border-gray-200 hover:border-blue-400 bg-white shadow-none min-w-[170px]"
                    size="sm"
                    style={{ boxShadow: "none" }}
                  >
                    <span className="inline-flex items-center justify-center w-8 h-8 rounded-full bg-blue-100 text-blue-700">
                      <Bot className="w-5 h-5" />
                    </span>
                    <span
                      className="font-semibold text-lg text-gray-800 truncate max-w-[100px]"
                      style={{ textOverflow: "ellipsis", overflow: "hidden" }}
                    >
                      {agentLabel.length > 12
                        ? agentLabel.slice(0, 10) + " ..."
                        : agentLabel}
                    </span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="start">
                  {availableAgents.map((agent) => (
                    <DropdownMenuItem
                      key={String(agent.id ?? "")}
                      onClick={() => toggleAgent(String(agent.id ?? ""))}
                      className={`flex items-center gap-2 ${
                        agent.isActive ? "bg-blue-100 text-blue-700" : ""
                      }`}
                    >
                      <span className="inline-flex items-center justify-center w-5 h-5 rounded-full bg-blue-50 text-blue-600">
                        <Bot className="w-3 h-3" />
                      </span>
                      <span className="truncate max-w-[100px]">
                        {agent.name || "Unnamed"}
                      </span>
                      {agent.isActive && (
                        <span className="ml-auto text-xs font-semibold">
                          Active
                        </span>
                      )}
                    </DropdownMenuItem>
                  ))}
                </DropdownMenuContent>
              </DropdownMenu>
            );
          })()
        ) : (
          <span className="text-red-500 text-xs">No agents available</span>
        )}
      </div>
      {/* Chat Header */}
      <div className="p-4 border-b bg-card">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Button
              variant="ghost"
              size="sm"
              onClick={toggleChatHistory}
              className="h-8 gap-2"
            >
              <FolderOpen className="w-4 h-4" />
              History
            </Button>
            <h2 className="text-lg font-semibold">
              {activeChat?.name || "AI Assistant"}
            </h2>
            <Button
              variant="ghost"
              size="sm"
              onClick={handleNewChat}
              className="h-8 gap-2"
            >
              <Plus className="w-4 h-4" />
              New Chat
            </Button>
          </div>

          <div className="flex items-center gap-2">
            {/* Active Agents */}
            {(Array.isArray(activeAgents) ? activeAgents : [])
              .filter((agent) => agent.isActive)
              .map((agent) => (
                <Badge
                  key={String(agent.id ?? "")}
                  variant="secondary"
                  className="text-xs"
                >
                  <Bot className="w-3 h-3 mr-1" />
                  {agent.name}
                </Badge>
              ))}

            {/* Chat Menu */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
                  <MoreVertical className="h-4 w-4" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuItem onClick={handleNewChat}>
                  <Plus className="mr-2 h-4 w-4" />
                  New Chat
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem
                  onClick={handleClearChat}
                  className="text-red-600"
                >
                  <Trash2 className="mr-2 h-4 w-4" />
                  Clear Chat
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>

        {/* Chat Info */}
        <div className="mt-2 flex items-center justify-between text-xs text-muted-foreground">
          <span>{messages.length} messages</span>
          <span>Click + to start a new conversation</span>
        </div>
      </div>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto">
        {!activeChat && !isTyping ? (
          <div className="flex items-center justify-center h-32">
            <Spinner size={28} />
          </div>
        ) : (
          <ScrollArea className="flex-1">
            <div className="p-4 space-y-4">
              {messages
                .filter(
                  (message) => message.content && message.content.trim() !== ""
                )
                .map((message, idx) => (
                  <ChatMessage key={message.id || idx} message={message} />
                ))}
              {isTyping && <TypingIndicator agentName="AI Agent" />}
            </div>
          </ScrollArea>
        )}
      </div>

      {/* Input Area */}
      <div className="p-4 border-t bg-card">
        <div className="flex gap-2">
          <div className="flex-1">
            <Textarea
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyDown={async (e) => {
                if (e.key === "Enter" && !e.shiftKey) {
                  e.preventDefault();
                  if (streaming) {
                    await handleSendMessageStream();
                  } else {
                    await handleSendMessage();
                  }
                }
              }}
              placeholder="Type your message..."
              className="flex-1"
              rows={2}
            />
          </div>
          <Button
            onClick={streaming ? handleSendMessageStream : handleSendMessage}
            disabled={isTyping || isStreaming}
          >
            <Send className="w-4 h-4" />
          </Button>
          <Button
            onClick={handleWorkflow}
            disabled={!inputValue.trim() || isTyping}
            size="sm"
            className="h-[60px] bg-blue-600 text-white"
          >
            Run Workflow
          </Button>
        </div>
        <div className="flex items-center justify-between mt-2 text-xs text-muted-foreground">
          <span>Press Enter to send, Shift+Enter for new line</span>
          <span>Commands: /clear, /reset, /help</span>
        </div>
      </div>

      <div className="flex items-center gap-2 mb-2">
        <Button
          variant={streaming ? "default" : "outline"}
          size="sm"
          onClick={() => setStreaming((s) => !s)}
        >
          {streaming ? "Streaming Mode" : "Normal Mode"}
        </Button>
      </div>

      {isStreaming && (
        <div className="mt-2 text-sm text-muted-foreground">
          AI is typing...
        </div>
      )}
      {streamedText && (
        <div className="mt-2 p-2 bg-muted rounded text-sm whitespace-pre-wrap border">
          {streamedText}
        </div>
      )}
    </div>
  );
}

// Helper to extract terminal command from agent response
function extractTerminalCommand(content: string): string | null {
  // Look for code blocks with shell/bash or lines starting with $ or common commands
  const codeBlockMatch = content.match(/```(?:shell|bash|sh)?\n([\s\S]*?)```/);
  if (codeBlockMatch) {
    return codeBlockMatch[1].trim();
  }
  const lineMatch = content.match(/\$\s*(.+)/);
  if (lineMatch) {
    return lineMatch[1].trim();
  }
  // Common commands
  const commonCmds = [
    "npm install",
    "yarn add",
    "pnpm add",
    "pip install",
    "apt-get install",
  ];
  for (const cmd of commonCmds) {
    if (content.includes(cmd))
      return cmd + content.split(cmd)[1].split(/\s|\n/)[0];
  }
  return null;
}
